.wp-block-vortex-search-form {
	margin-bottom: 4rem;
	border-radius: 0.5rem;
	--tw-bg-opacity: 1;
	background-color: rgb(248 113 113 / var(--tw-bg-opacity));
	padding: 2rem;
	--tw-text-opacity: 1;
	color: rgb(255 255 255 / var(--tw-text-opacity));
	--tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
	--tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
	box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  }
  .wp-block-vortex-search-form h1 {
	margin-bottom: 1rem;
	font-size: 1.5rem;
	line-height: 2rem;
	font-weight: 700;
	color: inherit;
  }
  .wp-block-vortex-search-form form {
	align-items: center;
	justify-content: space-between;
	overflow: hidden;
	border-radius: 0.5rem;
	--tw-bg-opacity: 1;
	background-color: rgb(255 255 255 / var(--tw-bg-opacity));
	padding-left: 0.5rem;
	padding-right: 0.5rem;
	padding-top: 0.25rem;
	padding-bottom: 0.25rem;
  }
  @media (min-width: 640px) {
	.wp-block-vortex-search-form form {
	  display: flex;
	}
  }
  .wp-block-vortex-search-form form input {
	flex-grow: 1;
	border-style: none;
	padding-left: 0.5rem;
	padding-right: 0.5rem;
	font-size: 1rem;
	line-height: 1.5rem;
	--tw-text-opacity: 1;
	color: rgb(156 163 175 / var(--tw-text-opacity));
	outline: 2px solid transparent;
	outline-offset: 2px;
  }
  .wp-block-vortex-search-form .btn-wrapper {
	margin-left: auto;
	margin-right: auto;
	align-items: center;
  }
  .wp-block-vortex-search-form .btn-wrapper > :not([hidden]) ~ :not([hidden]) {
	--tw-space-x-reverse: 0;
	margin-right: calc(1rem * var(--tw-space-x-reverse));
	margin-left: calc(1rem * calc(1 - var(--tw-space-x-reverse)));
  }
  .wp-block-vortex-search-form .btn-wrapper {
	border-radius: 0.5rem;
	padding-left: 0.5rem;
	padding-right: 0.5rem;
  }
  .wp-block-vortex-search-form .btn-wrapper button {
	border-radius: 0.5rem;
	border-style: none;
	--tw-bg-opacity: 1;
	background-color: rgb(248 113 113 / var(--tw-bg-opacity));
	padding-left: 1rem;
	padding-right: 1rem;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	font-size: 1rem;
	line-height: 1.5rem;
	--tw-text-opacity: 1;
	color: rgb(255 255 255 / var(--tw-text-opacity));
	outline: 2px solid transparent;
	outline-offset: 2px;
  }
